
.home-right{
  position: relative;
  width: 100%;
  height : 100vh;
  background:#9e9696; /* fallback for old browsers */
  
}

.border-child{
  height:100%;
}

.middle-child{
 height:100%;
}



.big{
  visibility: hidden;
  display: none;
}

.parent{
    
        height: 100%;
       /* position: fixed;*/
        display:flex;
        z-index: 1;
        top: 0;
        
        
    
}

.big-screen{
  position:absolute;
  visibility:visible;
  display: block;
}

  
  .item {
    left : 0;
    width: 25%;
    height:100vh;
    background: #280654;
    background: black;
    border-right: red solid 2px;
    /*position: fixed;*/
    z-index: 1;
    
  }

  .contactPic{
      
      border-radius: 100%;
      top:0px;
      position: absolute;
      width:20%;
      display:flex;
      justify-content: center;

  }


  .image{
    
    clip-path:circle(50%);
    height : 80%;
    width : 80%;
    object-fit: contain;
    margin-left:10%;
    margin-top:30px;
    
  }

  .last {
  right : 0;
  color: #222; 
  width:75%;
  height : 100vh;
  background-color: #a55c1b;
  background-image: linear-gradient(315deg, #a55c1b 0%,#1d0909 74%);
  background: #23074d; /* fallback for old browsers */
  background: linear-gradient(to right, #2e0769,#632617); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right,black,#B82E1F);
  display : flex;
  
  
  
  
}

.rightSide{
  
    width: 100%;
    
  
}

.description{

    
    width :50%;
    justify-content: center;
    align-items: center;
    
    
}

.DescriptionBox{
    width: 100%;
    height : 100vh;
    display: flex;
    margin-left:10%;
    
    margin-top: 120px;
}

.font{
    font-size: clamp(20px, 8vw, 80px);
    color:white;
}

.childFont{
    color:pink;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin: 10px 0px 0px;
    
    
    
    
}

.contactIcon{
     bottom:0px;
     margin-left: 35%;
     position: absolute;
     width:20%;
    display:flex;
    justify-content: center;
}

.iconFamily{
    color:white;
    color: peachpuff;
}

ul {
    list-style-type: none;
    margin: 0;
  padding: 0;
}

ul li{
    padding:5px;
}




