.about-parent{
    position : relative;
    display : flex;
    width : 100%;
    background: linear-gradient(to right,#5205c7,#673326);
    background: linear-gradient(to right,#B82E1F,#000000);
    background: black;
}

.about-left{
    display : flex;
    color:white;
    background: linear-gradient(315deg, #00f1e1 0%, #593bac 74%);
    background: linear-gradient(to left, #4e1199,#843028); 
    background: black;
    background: linear-gradient(to right,#B82E1F,#000000);
    width : 15%;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    justify-content: center;
    align-items: flex-end;
    clip-path: polygon(30% 0%, 100% 0%, 100% 100%,30% 100%, 0 70%,0% 30%);
  
            
}

.about-left p{
    color: pink;
    font-size: 2em;
    font-family:sans-serif;
    letter-spacing:2px;
    text-transform:uppercase;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    
}

.about-right{
    display:flex;
    
    justify-content: center;
    align-items: center;
    flex-direction : column;
    height :100vh;
    width:85%;
    background: #23074d; /* fallback for old browsers */
    background: linear-gradient(to right, #2e0769,#632617); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-color: #89d4cf;
    background: linear-gradient(315deg, #89d4cf 0%, #6e45e1 74%);
    background: linear-gradient(to right,#B82E1F,#000000);

    

    
    

  
}

.about-right p{
    margin: 0em;
       
        font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        
        max-width: 35ch;
        font-size: 12px;
        color: rgb(190, 184, 184);
        text-align: justify;
        text-justify: inter-word;
        text-indent: left;
        
}

.about-right h3{
    font-size: 30px;
    color:white;
    
}

.headingText{
    color: white;
    flex-direction: row-reverse;
}

@media only screen and (min-width : 1000px){

    .about-left p{
        color: pink;
        font-size: 5em;
        font-family:sans-serif;
        letter-spacing:2px;
        text-transform:uppercase;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke-width: 2px;
        
    }

    .about-right{
        display:flex;
        
        justify-content: center;
        align-items: center;
        flex-direction : column;
        height :100vh;
        width:85%;
        background: #23074d; /* fallback for old browsers */
        background: linear-gradient(to right, #2e0769,#632617); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        background-color: #89d4cf;
        background: linear-gradient(315deg, #89d4cf 0%, #6e45e1 74%);
        background: linear-gradient(to right,#B82E1F,#000000);
    
        
    
        
        
    
      
    }
    
    .about-right p{
        margin: 0em;
           
            font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            
            max-width: 55ch;
            font-size: 17px;
            color: rgb(190, 184, 184);
            text-justify: inter-word;
            text-indent: left;
            
    }
    
    .about-right h3{
        font-size: 30px;
        color:white;
        
    }
    
    .headingText{
        color: white;
        flex-direction: row-reverse;
    }
}    