

.Topmenu{
 
    display:flex;
    margin-left:230px;
    width :10%;
    margin-top : 25px;
    transform: rotate(90deg);
    height : 25px;

   }

   .top-view{
       position: absolute;
   }

   .resume-view{
       display: none;
       visibility: hidden;
   }
   
   .Topmenu div{
       padding: 2px;
       margin: 3px;
       background-color: peru;
   }

@media only screen and (min-width : 1000px){
.Topmenu{
 
 display:flex;
 flex-direction: row-reverse;
 height :30px;
 width :75%;
 margin-top : 25px;
 transform: none;
 margin-right: 30px;

 

}

.top-view{
    position: relative;
    margin-right:5px;
}

.resume-view{
    display: block;
    visibility: visible;
}

.Topmenu div{
    padding: 2px;
    margin: 8px;
    background-color: peru;
}
}