.skills-parent{
    position : relative;
    display : flex;
    width : 100%;
    height :100vh;
    background: linear-gradient(to right,#5205c7,#673326); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: black;
    

}

.skills-left{
    position: relative;
    display : flex;
    justify-content: center;
    align-items: center;
    width:85%;
    height : 100vh;
    background: linear-gradient(to right,#5205c7,#673326); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right,#5f1911,#000000);
    
}


.skills-right{
  
    display: flex;
    width :15%;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    justify-content: center;
    align-items: flex-end;
    background-color: thistle;
    background-image: linear-gradient(300deg, #43cac1 0%, #582fc9 74%);
    background: linear-gradient(to left, #4e1199,#843028); 
    background: linear-gradient(to right,#5f1911,#000000);
    clip-path: polygon(30% 0%, 100% 0%, 100% 100%,30% 100%, 0 70%,0% 30%);

}

.skills-right p{
    color: white;
    color : pink;
    font-size: 2em;
    font-family:sans-serif;
    letter-spacing:2px;
    text-transform:uppercase;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
}




.skillGrid{
    color:white;
    display :grid;
    grid-template-columns: auto auto ;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    height : 60%;
    width : 65%;
    
}

@media only screen and (min-width : 1000px){


    .skills-parent{
        position : relative;
        display : flex;
        width : 100%;
        height :100vh;
        background: linear-gradient(to right,#5205c7,#673326); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        background: black;
        
    
    }
    
    .skills-left{
        position: relative;
        display : flex;
        justify-content: center;
        align-items: center;
        width:85%;
        height : 100vh;
        background: linear-gradient(to right,#5205c7,#673326); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        background: linear-gradient(to right,#5f1911,#000000);
        
    }
    
    
    .skills-right{
      
        display: flex;
        width :15%;
        writing-mode: vertical-rl;
        text-orientation: mixed;
        justify-content: center;
        align-items: flex-end;
        background-color: thistle;
        background-image: linear-gradient(300deg, #43cac1 0%, #582fc9 74%);
        background: linear-gradient(to left, #4e1199,#843028); 
        background: linear-gradient(to right,#5f1911,#000000);
        clip-path: polygon(30% 0%, 100% 0%, 100% 100%,30% 100%, 0 70%,0% 30%);
    
    }
    
    .skills-right p{
        color: white;
        color : pink;
        font-size: 5em;
        font-family:sans-serif;
        
        letter-spacing:2px;
        text-transform:uppercase;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke-width: 2px;
    }
    
    
    
    
    .skillGrid{
        color:white;
        display :grid;
        grid-template-columns: auto auto auto auto ;
        grid-column-gap: 20px;
        grid-row-gap: 20px;
        height : 60%;
        width : 60%;
        
    }
    

}











