.work{
    
    position: relative;
    display : flex;
    justify-content: center;
    align-items: center;
    height : 100vh;
    width:85%;
    background: #23074d; /* fallback for old browsers */
    background: linear-gradient(to right, #2e0769,#632617); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-color: #89d4cf;
    background-image: linear-gradient(315deg, #89d4cf 0%, #6e45e1 74%);
    background: linear-gradient(to right, #2e0769,#54251a); 
    background: linear-gradient(to right,#5f1911,#000000);
    
    
}

.work-grid{
    display : grid;
    height : 50%;
    width : 65%;
    grid-template-columns: auto;
    grid-row-gap:20px;
    
    
}

.work-parent{
    display : flex;
    position : relative;
    width : 100%;
    height :100vh;
    background: linear-gradient(315deg, #89d4cf 0%, #6e45e1 74%);
    background: linear-gradient(to right, #2e0769,#632617);
    background: linear-gradient(to right,#B82E1F,#000000);
    background:black;
    
}

.work-right{
    display: flex;
    width : 15%;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    justify-content: center;
    align-items: flex-end;
    background-color: thistle;
    background-image: linear-gradient(300deg, #43cac1 0%, #582fc9 74%);
    clip-path: polygon(30% 0%, 100% 0%, 100% 100%,30% 100%, 0 70%,0% 30%);
    background: linear-gradient(to left, #4e1199,#843028); 
    background: linear-gradient(to right,#5f1911,#000000);

}

.work-right p{
    color: white;
    color : pink;
    font-size: 2em;
    font-family:sans-serif;
    letter-spacing:2px;
    text-transform:uppercase;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    
}



.work-grid div{
    background-color: #583e8e; 
    background: linear-gradient(to right,#000000,#50211c); ;
    color: #b5afbf;
    font-family:Arial, Helvetica, sans-serif;
    font-size:13px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: oldlace solid 2px;
    
}

.work-grid div p{
    margin: 0em;
}

.heading-work{
    font-weight:bold;
    font-size : 12px;
    color:#ded7d7;
}

@media only screen and (min-width : 1000px){

    .work-right p{
        color: white;
        color : pink;
        font-size: 5em;
        font-family:sans-serif;
        letter-spacing:2px;
        text-transform:uppercase;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke-width: 2px;
        
    }
    
    
    
    .work-grid div{
        background-color: #583e8e; 
    
        background: linear-gradient(to right,#000000,#50211c); ;
        color: #b5afbf;
        font-family:Arial, Helvetica, sans-serif;
        font-size:13px;
        border-top-left-radius: 30px;
        border-bottom-right-radius: 30px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: transparent;
        clip-path: polygon(20% 0%,100% 0,80% 100%,0 100%);

    }

    .heading-work{
        font-weight:bold;
        font-size : 15px;
        color:#ded7d7;
    }

    .work-grid{
        display : grid;
        height : 50%;
        width : 35%;
        grid-template-columns: auto;
        grid-row-gap:20px;
        
        
    }
    

}



