

.project{
   
    position: relative;
    display : flex;
    justify-content: center;
    align-items: center;
    height : 100vh;
    width:85%;
    background: linear-gradient(to right, #2e0769,#632617); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right,#B82E1F,#000000);
    
    
}

.project-parent{
    display : flex;
    position : relative;
    width : 100%;
    height :100vh;
    background: linear-gradient(to right, #2e0769,#632617); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right,#B82E1F,#000000);
    background: black;
    
}


.project-right{
     
    background: linear-gradient(to left, #5e14b9,#843028); 
    width : 15%;
    display : flex;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    justify-content: center;
    align-items: flex-end;
    clip-path: polygon(30% 0%, 100% 0%, 100% 100%,30% 100%, 0 70%,0% 30%);
    background: linear-gradient(to right,#B82E1F,#000000);
    
}



.project-right p{
    color: white;
    color:pink;
    font-size: 2em;
    font-family:sans-serif;
    letter-spacing:2px;
    text-transform:uppercase;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px;
    
}

.projectGrid{
    display :grid;
    grid-template-columns: auto auto;
    grid-column-gap: 3px;
    grid-row-gap: 2px;
    height : 80%;
    width : 80%;
}

.projectGrid div{
    
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.project-grid-front{
    
    height :100%;
    width : 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}

.front-image{
    height:120px;
    width : 120px;
    border-width: 1px;
    border-color: plum;
    border: 2px solid peachpuff;
}

.project-grid-back{
    
    
    display: flex;
    position : relative;
    width : 120px;
    height : 120px;
    flex-direction: column;
    border: 2px solid peachpuff;
    color:peachpuff;
    padding:2px;
    


}




.project-grid-back p{
    text-align:justify;
    padding:5px;
    font-size: 6px;
}

.project-footer{
    color: white;
    font-weight:bold;
    font-size: 8px;
}



@media only screen and (min-width : 1000px){

    .projectGrid{
        display :grid;
        grid-template-columns: auto auto auto ;
        grid-column-gap: 3px;
        grid-row-gap: 3px;
        height : 80%;
        width : 80%;
    }
    

    .project-grid-front{
    
        height :100%;
        width : 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    
    }
    
    .front-image{
        height:300px;
        width : 300px;
        border-width: 3px;
        border-color: plum;
        border: 5px solid peachpuff;
    }
    
    .project-grid-back{
        
        
        display: flex;
        position : relative;
        width : 300px;
        height : 300px;
        flex-direction: column;
        border: 5px solid peachpuff;
        color:peachpuff;
        padding:4px;
        
    
    
    }

    .project-grid-back p{
        text-align:justify;
        padding:5px;
        font-size: 12px;
    }

    .project-footer{
        color:white;
    font-weight:bold;
    font-size : 15px;
    }

    .project-right p{
        color: white;
        color:pink;
        font-size: 5em;
        font-family:sans-serif;
        letter-spacing:2px;
        text-transform:uppercase;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke-width: 2px;
        
    }

    

}