.home{
  display:flex;
  flex-direction: row;
  
}

.left-home{
    width : 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
    background-color: silver;
    
   }

.right-home{

  width : 80%;

}

@media only screen and (min-width : 1000px){
  .home{
    display:flex;
    flex-direction: row;
    
  }
  
  .left-home{
      width : 20%;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      flex-direction: column;
      background-color: silver;
      
     }
  
  .right-home{
  
    width : 80%;
  
  }
}